.chart-container{width:100%;background:var(--bg-secondary);border:1px solid var(--border);border-radius:var(--radius);padding:var(--spacing-16);position:relative}.chart-container__chart{width:100%;position:relative}.chart-container__chart canvas{border-radius:var(--radius-sm)}.chart-container__mini-info{display:flex;justify-content:space-between;align-items:center;margin-top:var(--spacing-8);padding-top:var(--spacing-8);border-top:1px solid var(--border);font-size:var(--font-size-xs)}.chart-container__mini-latest{color:var(--text-secondary)}.chart-container__mini-latest strong{color:var(--text-primary);font-weight:var(--font-weight-semibold)}.chart-container__mini-status{display:inline-flex;align-items:center;justify-content:center;width:20px;height:20px;border-radius:50%;font-size:var(--font-size-sm);font-weight:var(--font-weight-bold)}.chart-container__mini-status.optimal{background:var(--success);color:var(--text-primary)}.chart-container__mini-status.high{background:var(--danger);color:var(--text-primary);animation:pulse-danger 2s infinite}.chart-container__mini-status.low{background:var(--warning);color:var(--bg-primary);animation:pulse-warning 2s infinite}.chart-container--mini{padding:var(--spacing-12);min-height:120px}.chart-container--mini .chart-container__chart{height:80px !important}.chart-container--mini:hover{border-color:var(--accent);cursor:pointer;transition:border-color .2s ease}.chart-container--loading::before{content:"";position:absolute;top:0;left:0;right:0;bottom:0;background:var(--surface-dark-dim);display:flex;align-items:center;justify-content:center;border-radius:var(--radius);z-index:10}.chart-container--loading::after{content:"Se încarcă...";position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);color:var(--text-secondary);font-size:var(--font-size-sm);z-index:11}.chart-container--error{border-color:var(--danger);background:var(--danger)10}.chart-container--error .chart-container__error{display:flex;align-items:center;justify-content:center;height:100px;color:var(--danger);font-size:var(--font-size-sm);text-align:center}.chart-container--no-data .chart-container__no-data{display:flex;flex-direction:column;align-items:center;justify-content:center;height:100px;color:var(--text-muted);font-size:var(--font-size-sm);text-align:center}.chart-container--no-data .chart-container__no-data-icon{font-size:var(--font-size-xl);margin-bottom:var(--spacing-8);opacity:.5}@keyframes pulse-danger{0%,100%{transform:scale(1);opacity:1}50%{transform:scale(1.1);opacity:.8}}@keyframes pulse-warning{0%,100%{transform:scale(1);opacity:1}50%{transform:scale(1.1);opacity:.8}}@media(max-width: 768px){.chart-container{padding:var(--spacing-12)}.chart-container--mini{padding:var(--spacing-8);min-height:100px}.chart-container--mini .chart-container__chart{height:60px !important}.chart-container--mini .chart-container__mini-info{font-size:var(--font-size-xxs)}.chart-container--mini .chart-container__mini-status{width:16px;height:16px;font-size:var(--font-size-xs)}}@media(prefers-contrast: high){.chart-container{border-width:2px}.chart-container__mini-status{border:2px solid var(--text-primary)}.chart-container__mini-status.optimal{border-color:var(--success)}.chart-container__mini-status.high{border-color:var(--danger)}.chart-container__mini-status.low{border-color:var(--warning)}}@media print{.chart-container{border:1px solid #000;background:#fff}.chart-container__mini-status.optimal::after{content:" (Optim)"}.chart-container__mini-status.high::after{content:" (Ridicat)"}.chart-container__mini-status.low::after{content:" (Scăzut)"}}